<template>
  <nav id="r-sidebar" class="myflex-column" v-bind:class="{barRtoggle: $store.state.isRActive}">
    <div class="r-header r-style">
      <span>About this Channel</span>
      <div style="flex-grow: 1;justify-content: flex-end;display: flex;">
        <a @click="RSidebarClose">
          <i class="im im-x-mark"></i>
        </a>
      </div>
    </div>
    <!-- content wrapper -->
    <div style="overflow:auto;">
      <!-- 채널 메뉴 시작 -->
      <a class="r-style" v-b-toggle.channel-info>
        <i class="im im-info"></i>
        <span style="margin-left:20px;">Channel Details</span>
        <div style="display: flex; flex-grow: 1; justify-content: flex-end;">
          <i class="im im-care-down" style="font-size: 15px;"></i>
        </div>
      </a>
      <!-- 채널 메뉴 끝 -->
      <!-- 채널 메뉴 콜랩스 -->
      <b-collapse id="channel-info">
        <div class="s-coll-style">
          <div>
            <div style="display:flex;">
              <p>Channel Name</p>
              <template v-if="modalObj.currentChannel.member_email == $store.state.currentUser.email">
                <a class="verti-align" data-mode="edit" @click="prepareModal">Edit</a>
                <a class="verti-align" @click="deleteChannel">Delete</a>
              </template>
            </div>
            <li class="list-unstyled">{{ modalObj.currentChannel.name }}</li>
          </div>
          <div style="display:flex; justify-content:flex-start;">
            <b-button variant="primary" @click="leaveChannle">나가기</b-button>
          </div>
        </div>
      </b-collapse>

      <!-- Users 메뉴 시작 -->
      <a class="r-style" v-b-toggle.user-info>
        <i class="im im-users"></i>
        <span style="margin-left:20px;">{{channelUserSize}} Users</span>
      </a>
      <!-- Users 메뉴 끝 -->

      <!-- to do list 메뉴 시작 -->
      <a class="r-style">
        <i class="im im-task-o"></i>
        <span style="margin-left:20px;">Todo List</span>
      </a>
      <!-- to do list 메뉴 끝 -->
      <!-- calender 메뉴 시작 -->
      <a class="r-style">
        <i class="im im-calendar"></i>
        <span style="margin-left:20px;">Calendar</span>
      </a>
      <!-- calender 메뉴 끝 -->
    </div>
  </nav>
</template>
<script>
  export default {
    props: ['modalObj'],
    name: 'RSidebar',
    data() {
      return {
        channelUserSize: 0,
        userSelect: null
      }
    },
    mounted() {
      this.$eventBus.$on('channelUserSize', data => {
        this.channelUserSize = data
      })
    },
    methods: {
      leaveChannle: function () {
        this.$http.post('/api/channel/leave', {
          // 모두가 나가면 채널 삭제
          email: this.$store.state.currentUser.email,
          channel_id: this.modalObj.currentChannel.id
        }).then(res => {
          this.$alertModal('alert redirect', this.modalObj.currentChannel.name + ' 채널에서 나갔습니다.')
        }).catch(error => {
          this.$alertModal('error', '나가기에 실패했습니다.')
        })
      },
      RSidebarClose: function () {
        this.$store.state.isRActive = false
      },
      prepareModal: function (e) {
        if (e.target.dataset.mode == 'edit') {
          this.modalObj.modalTitle = '채널 수정'
          //Cha1는 나중에 현재 채널 정보에서 가져올 채널 이름값
          // this.modalObj.channelTitle ='Cha1'
          this.$emit('passData', this.modalObj)
          this.$bvModal.show('channelCU')
        }
      },
      msgBox: async function (content) {
        await this.$bvModal.msgBoxConfirm(content, {
          title: '확인',
          okTitle: '확인',
          okVariant: 'danger',
          buttonSize: 'sm',
          cancelTitle: '취소'
        })
          .then(value => {
            this.userSelect = value
            return value
          })
      },
      deleteChannel: async function () {
        //current vuex 사용
        await this.msgBox("정말로 채널을 삭제하시겠습니까?")
        console.log(this.userSelect)
        const user = this.$store.state.currentUser
        console.log(user)
        if (!this.userSelect) {
          return
        }
        if (this.modalObj.currentChannel.member_email == user.email) {
          await this.$http.post('http://localhost:9191/api/channel/delete', this.modalObj.currentChannel
          ).then(res => {
            console.log(res)
            this.modalObj.currentChannel = null
            this.modalObj.modalTitle = null
            this.$router.go('/main')
          }).catch(error => {
            console.log(error)
          })
        }
      }
    }
  }

</script>
